
html, body {
  height: 100%;
  min-height: 100vh;
  background-color: black;
}

.App {
  text-align: center;
  background-color: black;
  letter-spacing: 0.1em;
  ;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.pages {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  height: 100%;
  background-color: #000;
  color: white;
}

.logo {
  margin-right: 20px;
  height: 40px;
}

.lessons-page {
  color: white;
}

/*dropdown menu*/
.navbar {
  background-color: #000;
}
.blacky {
  background-color: #000;
}
.navbar-toggler-icon {
  width: 1em;
  height: 1em;
  background-image: url('./images/36-365828_navbar-toggle-icon-menu-hamburger-png-white.png');
}
.bg-dark {
  background-color: #000000 !important;
}
.mr-auto {
  background-color: #000;
}

.home_footer {
  background-color: grey;
  position: fixed;
  vertical-align: baseline;
}

.hm-1 {
  background-color: #000;
  text-align: center;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  width: auto;
}

